export default {
	data() {
		return {
			showPassword1:true,
			showPassword2:true,
			password1: '',
			password2: '',
			code: "",
			mobile: ""
		}
	},
	onLoad(options) {
		let that = this;
		that.code = options.code;
		that.mobile = options.mobile;
	},
	methods: {
		changePassword1() {
			let that = this;
			that.showPassword1 = !that.showPassword1;
		},
		changePassword2() {
			let that = this;
			that.showPassword2 = !that.showPassword2;
		},
		register(){
			let that = this;
			if (!that.$utils.common.passwordTest.test(that.password1)) {
				uni.showToast({
					title: '密码需是8-14位数字或字母',
					icon: 'none'
				})
			} else if (that.password1 != that.password2) {
				uni.showToast({
					title: '两次密码不一致',
					icon: 'none'
				})
			} else {
				uni.showLoading({
					title:"请稍等..."
				})
				var param = {
					mobile: that.mobile,
					password: that.password1,
					repassword: that.password2,
					vcode: that.code
				}
				that.$api.updatepassword(param).then(res => {
					uni.hideLoading();
					if (res.success) {
						uni.showToast({
							title: '修改成功',
							icon: 'success'
						})
						setTimeout(()=>{
							uni.navigateBack({
								delta: 2
							})
						},2000)
					} else {
						uni.showToast({
							title: res.message,
							icon: "none"
						})
					}	
				})
			
			}
		}
	}
}
